"use client";

import { ReactNode, useEffect } from "react";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import {
  Layout01Icon,
  Activity04Icon,
  PieChartIcon,
  ArchiveIcon,
} from "hugeicons-react";

import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { clearAuthFlag, isAuthenticated } from "@/lib/auth";

const NAV_ITEMS = [
  { href: "/diretoria", label: "Diretoria", icon: Layout01Icon },
  { href: "/producao", label: "Produção", icon: Activity04Icon },
  { href: "/comercial", label: "Comercial", icon: PieChartIcon },
  { href: "/estoque", label: "Estoque", icon: ArchiveIcon },
];

export default function DashboardLayout({
  children,
}: {
  children: ReactNode;
}) {
  const router = useRouter();
  const pathname = usePathname();

  useEffect(() => {
    if (!isAuthenticated()) {
      router.replace("/login");
    }
  }, [router]);

  function handleLogout() {
    clearAuthFlag();
    router.replace("/login");
  }

  return (
    <div className="flex min-h-screen bg-muted">
      <aside className="flex w-64 flex-col border-r border-sidebar-border bg-sidebar/90 px-4 py-5 backdrop-blur-xl">
        <div className="mb-6 flex items-center gap-2 px-1">
          <div className="flex h-8 w-8 items-center justify-center rounded-full bg-primary/15">
            <span className="text-sm font-semibold text-primary-foreground">
              EM
            </span>
          </div>
          <div>
            <p className="text-sm font-semibold tracking-tight">EM Vidros</p>
            <p className="text-[11px] text-muted-foreground">EM Hub</p>
          </div>
        </div>
        <nav className="space-y-1 text-sm">
          {NAV_ITEMS.map((item) => {
            const Icon = item.icon;
            const active = pathname === item.href;
            return (
              <Link
                key={item.href}
                href={item.href}
                className={`flex items-center gap-2 rounded-xl px-3 py-2 text-sm font-medium transition-colors ${
                  active
                    ? "bg-primary text-primary-foreground shadow-sm"
                    : "text-muted-foreground hover:bg-sidebar-accent hover:text-sidebar-foreground"
                }`}
              >
                <Icon className="h-4 w-4" />
                <span>{item.label}</span>
              </Link>
            );
          })}
        </nav>
        <div className="mt-auto flex items-center gap-3 pt-6 text-xs text-muted-foreground">
          <Avatar className="h-8 w-8">
            <AvatarFallback>EV</AvatarFallback>
          </Avatar>
          <div className="flex-1">
            <p className="font-medium text-foreground">Gestor EM Vidros</p>
            <p className="text-[11px] text-muted-foreground">
              diretoria@emvidros.com
            </p>
          </div>
          <Button
            type="button"
            size="icon"
            variant="outline"
            className="h-7 w-7 text-[11px]"
            onClick={handleLogout}
          >
            ⌘Q
          </Button>
        </div>
      </aside>
      <main className="flex-1">
        <header className="flex items-center justify-between border-b border-border bg-background/80 px-6 py-3 backdrop-blur-xl">
          <div>
            <p className="text-[11px] font-medium uppercase tracking-[0.16em] text-muted-foreground">
              Visão Geral
            </p>
            <p className="text-sm text-muted-foreground">
              Indicadores consolidados da operação EM Vidros
            </p>
          </div>
          <div className="flex items-center gap-3">
            <span className="rounded-full bg-muted px-3 py-1 text-[11px] text-muted-foreground">
              Período: Últimos 30 dias
            </span>
            <Button variant="outline" size="sm">
              Ajustar período
            </Button>
          </div>
        </header>
        <div className="px-6 py-5">
          <div className="mx-auto flex max-w-6xl flex-col gap-6">{children}</div>
        </div>
      </main>
    </div>
  );
}


